.menu-container
  height: 100%
  display: flex
  flex-direction: column
  header
    position: relative
    height: 55px
    padding: 10px 15px
    display: flex
    justify-content: space-between
    align-items: center
    border-bottom: 1px solid #ddd
    .left-tools
      width: 70px
      display: flex
      align-items: center
    h4
      margin: 0 !important
  main
    flex: 1
    overflow: hidden
    display: flex
    position: relative
    button.new-note
      position: absolute
      right: 20px
      bottom: 20px

#aside-checkbox, #aside-label
  display: none

.ant-menu
  border-right: none

.side-wrapper
  transition: all 0.5s
  width: 300px
  height: 100%
  display: flex
  flex-direction: column
  border-right: 1px solid #eee
  .side-menu
    flex: 1
    overflow-y: auto
    overflow-x: hidden
    border: none
  footer
    border-top: 1px solid #eee
    padding: 10px 15px
    overflow: hidden

@media screen and (max-width: 500px)
  aside
    display: flex
    flex-direction: row-reverse
    position: absolute
    height: 100%
    background: white
    z-index: 1000
  .side-wrapper
    width: 0
    box-shadow: 10px 0px 20px 5px #0001
    overflow: hidden
  #aside-label
    display: unset
    cursor: pointer
    margin-right: 5px
    padding: 5px 10px
  #aside-checkbox:checked + .side-wrapper
    width: 200px
  button.large
    display: none
  button.small
    display: unset !important

.tag-circle
  width: 12px
  height: 12px
  border-radius: 6px

.menu-item
  display: flex
  align-items: center
  justify-content: space-between
  & > .tag-circle
    margin: 0 12px 0 7px
  #all-note-icon
    margin: 0 10px 0 7px
  .tag-name
    margin: 0 auto 0 0 !important
  .tag-name-input
    width: initial
    flex: 1
  .ant-input-group-addon
    padding: 0 5px !important
  .ant-select-arrow
    transform: scale(0.8)
  .ant-select-selection-item
    display: flex
    align-items: center

button.team-btn
  margin-right: 10px
button.small
  display: none

.note-list
  flex: 1
  padding: 10px
  overflow-y: auto
  .list-item
    border-bottom: 1px solid #eee
    height: 4em
    display: flex
    align-items: center
    padding: 0 1em
    cursor: pointer
    .thumbnail-wrapper
      width: 3em
      margin-right: 1em
      display: flex
      justify-content: center
      position: relative
      .thumbnail
        max-width: 3em
        max-height: 3.5em
        border: 1px solid #eee
        &:not(.loaded) + .cloud-icon
          display: none
      .cloud-icon
        position: absolute
        bottom: 0
        left: 0
        transform: scale(0.7)
        transform-origin: bottom left
    .note-name
      flex: 1
      margin: 0
      padding: 6px
      overflow: hidden
      text-overflow: ellipsis
      display: -webkit-box
      -webkit-box-orient: vertical
      -webkit-line-clamp: 2
      color: rgba(0, 0, 0, 0.85)
    .note-name-input
      padding-left: 5px
      flex: 1

.tag-select
  display: flex
  align-items: center
  .tag-circle, .none-tag-icon
    margin-right: 10px
